<script>
  import { Table, TableColumn, Message, Input, Button, InputNumber, Dialog, Form, FormItem, Select, Option, Switch, Radio, RadioGroup, Col} from 'element-ui';
  import MyMoney from 'src/components/UIComponents/Money'
  import SelectAjax from 'src/components/UIComponents/SelectAjax'
  import MySelect from 'src/components/UIComponents/Select'
  import MyRegion from 'src/components/UIComponents/Region.vue'
  import { mapState } from 'vuex'
  import api from 'src/store/api';

  export default {
    components: {
      ElTable: Table,
      ElTableColumn: TableColumn,
      ElInput: Input,
      ElButton: Button,
      ElDialog: Dialog,
      ElForm: Form,
      ElFormItem: FormItem,
      ElInputNumber: InputNumber,
      ElSelect: Select,
      ElOption: Option,
      ElSwitch: Switch,
      ElRadio: Radio,
      ElRadioGroup: RadioGroup,
      ElCol: Col,
      SelectAjax,
      MyMoney,
      MySelect,
      Message,
      MyRegion
    },
    created() {
      let self = this;
      this.$validator.extend('required_region', {
        getMessage(field, val) {
          return 'Tỉnh/huyện không được rỗng'
        },
        validate(value, field) {
          if (!value.region && !value.subregion) return 0
          else return 1
        }
      })
    },
    data () {
      return {
        tableData: [],
        order: [],
        shippingFee: 0,
        coupon: '',
        couponDiscount: 0,
        notes: '',
        shippingPrice: 0,
        formCoupon: {
          type: '',
          discount: 0
        },
        formShipping: {
          region: 0,
          subregion: 0
        },
        formCustomer: {
          region: 0,
          subregion: 0
        },
        listProduct: [],
        customer: [],
        dialogFormCouponVisible: false,
        dialogFormShippingVisible: false,
        editInfoCustomer: false,
        editShippingCustomer: false,
        searchProduct: false,
        optionsStatusOrder: [
          {
            title: 'Mới',
            value: 'new'
          },{
            title: 'Xác nhận',
            value: 'confirm'
          },{
            title: 'Hoàn tất',
            value: 'done'
          }, {
            title: 'Hủy',
            value: 'cancel'
          }, {
            title: 'Trả lại',
            value: 'return'
          }, {
            title: 'Nháp',
            value: 'draft'
          }
        ],
        optionsStatusPay: [
          {
            title: 'Chưa thanh toán',
            value: 0
          },{
            title: 'Đã thanh toan',
            value: 1
          }
        ],
        optionsStatusShipping: [
          {
            title: 'Chưa giao hàng',
            value: 0
          },{
            title: 'Đang giao hàng',
            value: 1
          },{
            title: 'Đã giao hàng',
            value: 2
          }
        ],
      }
    },

    computed: {
      ...mapState(['orderDetail'])
    },

    mounted() {
      let id = this.$route.params.id;
      this.$store.dispatch('fetchOrderDetail', id);

      this.$store.dispatch('setPageTitle', 'Cập nhật đơn hàng');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Cập nhật',
        type: 'primary',
        icon: '',
        callback: this.save
      }])
    },

    methods: {
      cancel() {
        this.$router.push({name: 'AllOrders'});
      },
      save() {
        let self = this;
        this.$validator.validateAll().then(function (result) {
          if (result) {
            let data = {};
            data.order = self.order;
            data.customer = self.formCustomer;
            data.customer.region = self.formCustomer.dataRegion.region;
            data.customer.subregion = self.formCustomer.dataRegion.subRegion;
            data.shippingInfo = self.formShipping;
            data.shippingInfo.region = self.formShipping.dataRegion.region;
            data.shippingInfo.subregion = self.formShipping.dataRegion.subRegion;
            self.$store.dispatch('updateOrder', data).then(res => {
              Message({
                message: 'Cập nhật thành công',
                type: 'success'
              });
              self.$router.push({name: 'AllOrders'})
            }, res => {
              Message({
                message: res.message,
                type: 'danger'
              });
            });
          }
        })
      },
    },
    watch: {
      orderDetail(newVal, oldVal){
        this.tableData = newVal.cart;
        this.formCustomer = newVal.customer;
        this.formCustomer.dataRegion = {
          region: newVal.customer.region || 0,
          subRegion: newVal.customer.subregion || 0,
        }
        this.formShipping = newVal.shipping_info;
        this.formShipping.dataRegion = {
          region: newVal.shipping_info.region || 0,
          subRegion: newVal.shipping_info.subregion || 0,
        }
        this.notes = newVal.order.notes;
        this.order = newVal.order;
      }
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>

